.splitterContainer {
    border: solid #000 2px;
    min-width: 300px;
    width: 400px;   
    margin: 20px auto;
    opacity: 0;
    overflow: hidden;
    -webkit-transition: 1000ms;
    -o-transition: 1000ms;
    transition: 1000ms;
    -webkit-box-shadow:4px 4px 13px 0px #00000078;
            box-shadow:4px 4px 13px 0px #00000078;
}


