  background: -o-linear-gradient( top left, #2B2B2B, #919191);
  background: linear-gradient( to bottom right, #2B2B2B, #919191)}

.topImg {height: 65px; margin: -6px 30px; border: solid #fff 1px}

.topInfoRow {padding: 10px}


