
.switch {
    position: relative;
    display: inline-block;
    top: 7px;
    width: 48px;
    height: 20px;
  }

  .switch input {display:none;}
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 13px;
    width: 13px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    -webkit-box-shadow: 0 0 1px #2196F3;
            box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

  .rowOpt {
      background: 
        -webkit-gradient(
          linear, 
          left top, right bottom, 
          from(rgba(2, 2, 2, 0.5)), 
          to(rgba(126, 133, 143, 0.5))
        );
      background: 
        -webkit-linear-gradient(
          top left, 
          rgba(2, 2, 2, 0.5), 
          rgba(126, 133, 143, 0.5)
        );
      background: 
        -o-linear-gradient(
          top left, 
          rgba(2, 2, 2, 0.5), 
          rgba(126, 133, 143, 0.5)
        );
      background: 
        linear-gradient(
          to bottom right, 
          rgba(2, 2, 2, 0.5), 
          rgba(126, 133, 143, 0.5)
        );
      border-radius: 5px;
      margin: 5px auto;
  }

  .titleOpt {
      padding-top: 5px;
      padding-left: 20px;
      color: #fff;
  }


