
.card {min-width: 480px}

.controlBtn {
    border-radius: 0;
    border: 0;
    padding: 5px 5px;
    margin: 0; 
    background: none;  
}

.controlBtn:hover {border-radius: 0}

.controlBtn:disabled {background: none}

.controlContainer {
    padding: 0;
    overflow: hidden;
    border: solid #000 2px;
    border-radius: 5px
}

#btnContainer {
    border: solid #000 1px;
    background: -webkit-gradient(
            linear, 
            left top, right bottom, 
            from(rgb(22, 22, 24)), 
            to(rgb(99, 104, 111))
        );
    background: -webkit-linear-gradient(
            top left, 
            rgb(22, 22, 24), 
            rgb(99, 104, 111)
        );
    background: -o-linear-gradient(
            top left, 
            rgb(22, 22, 24), 
            rgb(99, 104, 111)
        );
    background: linear-gradient(
            to bottom right, 
            rgb(22, 22, 24), 
            rgb(99, 104, 111)
        );  
}

#fighter {border: solid #000 2px; Height: 150px; border-radius: 5px 5px 0 0}

#collapsed {border: solid #000 2px; height: 75px; border-radius: 0 0 5px 5px}

.controlFighter {
    position: relative;
    height: 150px;
    top: -10px;
    left: -16px;
}

.fighterTitle {
    right: 0px;
    color: #fff;
    padding-top: 10px;
    margin-bottom: -35px;  
    background: -webkit-gradient(
        linear, 
        left top, right bottom, 
        from(#16161880), 
        to(#63686f80)
    );  
    background: -webkit-linear-gradient(
        top left, 
        #16161880, 
        #63686f80
    );  
    background: -o-linear-gradient(
        top left, 
        #16161880, 
        #63686f80
    );  
    background: linear-gradient(
        to bottom right, 
        #16161880, 
        #63686f80
    ); 
}

.top {margin-top: 40px}

#controlTop {
    border: solid #000 1px;
    background-image: url("../../assets/img/games/mtpo/bgRing.png");
    background-size: 100%;
    overflow: hidden;
    height: 150px;
}

#gameSelect{
    padding-top: 5px;
    opacity: 1;
    -webkit-transition: 1500ms;
    -o-transition: 1500ms;
    transition: 1500ms;
    background: -webkit-gradient(
        linear, 
        left top, right bottom, 
        from(rgba(67, 154, 220, 0.3)), 
        to(rgba(205, 205, 205, 0.3))
    );
    background: -webkit-linear-gradient(
        top left, 
        rgba(67, 154, 220, 0.3), 
        rgba(205, 205, 205, 0.3)
    );
    background: -o-linear-gradient(
        top left, 
        rgba(67, 154, 220, 0.3), 
        rgba(205, 205, 205, 0.3)
    );
    background: linear-gradient(
        to bottom right, 
        rgba(67, 154, 220, 0.3), 
        rgba(205, 205, 205, 0.3)
    );
}

#fighterTop {
    position: absolute; 
    overflow: hidden;
    top: 0;
    padding: 0 15px;
    -webkit-transition: 3000ms;
    -o-transition: 3000ms;
    transition: 3000ms;
}

#expanded {height: 0; overflow: hidden; -webkit-transition: 1500ms; -o-transition: 1500ms; transition: 1500ms}

.fighterImg, .newTime {margin: 0; padding: 0}

.fighterName{padding: 3px; margin: 0 }

#topInfo { opacity: 1; -webkit-transition: 1500ms; -o-transition: 1500ms; transition: 1500ms }

.behind {
    color: rgb(210, 56, 0);
}

.gold {
    color: rgb(4, 0, 218);
}

.ahead {
    color: rgb(3, 122, 29);
}


