body {
    background: -webkit-gradient(linear, left top, right top, from(rgb(225, 69, 52)), to(rgb(205, 97, 85)));
    background: -webkit-linear-gradient(left, rgb(225, 69, 52), rgb(205, 97, 85));
    background: -o-linear-gradient(left, rgb(225, 69, 52), rgb(205, 97, 85));
    background: linear-gradient(to right, rgb(225, 69, 52), rgb(205, 97, 85));
 
}

#outer {
    height: 100vh;
    color: #fff;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}

#inner {
    width: 350px; 
    margin: auto;
}

.img-thumbnail {
   padding: 0;    
}

.loginBtn {
    position: relative;
    bottom: 15px;
    border: solid #fff 1px;
}

#tLogo {
    width: 15%; 
    padding-right: 10px;
}


